function userLink($compile, scope, user, keepGroupByField) {
    var href;
    if (scope.$parent.isGadget) {
        var reportParams = angular.copy(scope.$parent.reportParamsObject);
        delete reportParams.allUsers;
        delete reportParams.group;
        if (!keepGroupByField) {
            delete reportParams.groupByField;
        }
        reportParams.pivotTableType = 'Timesheet';
        reportParams.user = user.key;
        scope.reportParams = $.param(reportParams);
        // CAUTION: malformated string to be inserted between double quotes
        href = '{{$parent.hostBaseUrl}}/plugins/servlet/ac/timereports/timereports#!{{reportParams}}" target="_parent';
    } else {
        href = "{{userObject.key ? '#/?pivotTableType=Timesheet&user=' + userObject.key + '&group=" + (keepGroupByField ? "" : "&groupByField=") + "&_=' + $id : ''}}";
    }
    scope.userObject = user;
    var link = $compile('<a ng-class="{inactive: !userObject.active, disabled: !userObject.key}" ng-href="' + href + '" ng-bind="\'test\'"></a>')(scope);
    delete scope.reportParams;
    delete scope.userObject;
    return link;
}

app.directive('issueField', ['$compile', 'applicationLoggingService', function($compile, logger) {
    return {
        restrict: 'E',
        scope: {
            field: '=',
            name: '=',
            issue: '=',
            jiraConfig: '='
        },
        link: function(scope, element, attrs) {
            var NULL_VALUE = "&nbsp;";
            var isSecondsField = function(field) {
                return ['aggregatetimeoriginalestimate',
                        'aggregatetimeestimate',
                        'aggregatetimespent',
                        'timeoriginalestimate',
                        'timeestimate',
                        'timespent']
                        .indexOf(field) != -1;
            };
            var sprintRegex = /com\.atlassian\.greenhopper\.service\.sprint\.Sprint@[\d\w]+\[(.+)?\]/;
            var m;
            var getNameOrKey = function(field) {
                if (angular.isObject(field) && field.hasOwnProperty('name')) {
                    return field.name;
                } else if (angular.isObject(field) && field.hasOwnProperty('value')) {
                    return field.value + (field.hasOwnProperty('child') ? ':' + getNameOrKey(field.child) : '');
                } else if (angular.isObject(field) && field.hasOwnProperty('key')) {
                    scope.issueField = field;
                    scope.hostBaseUrl = scope.$parent.hostBaseUrl;
                    return $compile('<issue-key issue="issueField" show-title="true"/>')(scope);
                    delete scope.issueField;
                } else if (angular.isString(field) && (m = field.match(sprintRegex))) {
                    logger.trace("Sprint field", m[1]);
                    var splits = m[1].split(',');
                    for (var i = 0; i < splits.length; i++) {
                        var split = splits[i];
                        logger.trace("Sprint field split", split);
                        if (split.indexOf('name=') == 0) {
                            return split.substr(5);
                        }
                    }
                    return NULL_VALUE;
                } else if(isSecondsField(scope.name)) {
                    return DrawHelper.getFormattedTime(field, scope.jiraConfig);
                } else {
                    return field;
                }
            };

            var value;
            if (!scope.field) {
                if (scope.name == 'projectcategory' && scope.issue && scope.issue.fields.project.projectCategory) {
                    value = scope.issue.fields.project.projectCategory.name;
                } else {
                    value = '&nbsp;';
                }
            } else if (/\/rest\/api(\/\d+)\/user/.test(scope.field.self)) {
                // FIXME: code duplication
                var href;
                if (scope.$parent.isGadget) {
                    var reportParams = angular.copy(scope.$parent.reportParamsObject);
                    delete reportParams.allUsers;
                    delete reportParams.group;
                    delete reportParams.groupByField;
                    reportParams.pivotTableType = 'Timesheet';
                    reportParams.user = scope.field.key;
                    scope.reportParams = $.param(reportParams);
                    // CAUTION: malformated string to be inserted between double quotes
                    href = '{{$parent.hostBaseUrl}}/plugins/servlet/ac/timereports/timereports#!{{reportParams}}" target="_parent';
                } else {
                    href = '#/?pivotTableType=Timesheet&user={{field.key}}&allUsers=&group=&groupByField=&_={{$id}}';
                }
                value = $compile('<a ng-class="{inactive: !field.active, disabled: !field.key}" ng-href="' + href + '" ng-bind="field.displayName || field.name"></a>')(scope);
                // TODO: value = userLink($compile, scope, scope.field);
            } else if (TimesheetUtils.isDate(scope.field)) {
                scope.value = new Date(scope.field);
                value = $compile("<span>{{value | date:'yyyy-MM-dd'}}</span>")(scope);
            } else if (Array.isArray(scope.field)) {
                value = '';
                scope.field.forEach(function(v) {
                    if (value.length > 0) {
                        value += ', ';
                    }
                    value += getNameOrKey(v);
                });
            } else {
                value = getNameOrKey(scope.field);
            }
            element.append(value);
        }
    }
}]);
app.directive('pivotCell', ['$compile', '$interval', function($compile, $interval) {
    return {
        restrict: 'E',
        scope: {
            key: '=',
            columns: '=',
            logWork: '=',
            editWork: '=',
            deleteWork: '=',
            jiraConfig: '='
        },
        link: function(scope, element, attrs) {
            var editDelete = $compile('<span><i class="fa fa-pencil-square-o" ng-click="editWork(worklog)"></i></span>' +
                '<span><i class="fa fa-trash-o" ng-click="deleteWork(worklog)"></i></span>')(scope);
            if (scope.logWork && scope.key.keyValue == '3action') {
                var column = scope.columns[scope.key.keyValue];
                var html;
                if (column.isTotalColumn) {
                    html = "&nbsp;";
                } else if (column.entries) {
                    scope.worklog = column.entries[0].worklog.issueKey
                        ? column.entries[0].worklog // existing worklog from issue picker
                        : {issueKey: column.entries[0].rowKey.keyValue}; // new worklog
                    column.worklog = scope.worklog; // pass to timesheetController#startWork()
                    html = $compile('<i class="fa fa-play" ng-click="logWork()" ng-show="!$root.workTimer && key.showStart"></i>')(scope);
                } else if (column.worklog.id) {
                    scope.worklog = column.worklog;
                    html = editDelete;
                } else { // new worklog
                    scope.entry = column;
                    scope.worklog = column.worklog;
                    html = $compile('<span><i class="fa" ng-click="logWork()" ng-class="{\'fa-play\': !$root.workTimer, \'fa-pause\': $root.workTimer && !worklog.stopped}"></i></span>' +
                        '<span ng-if="!$root.workTimer || !worklog.stopped"><i class="fa fa-stop" ng-click="logWork(false, true)"></i></span>')(scope);
                    scope.$watch('worklog.id', function(newValue) { // not used
                        if (newValue) {
                            element.html(editDelete);
                        }
                    });
                }
                element.append(html);
            } else if (scope.key.filterName) {
                element.append($compile('<span ng-bind-html="columns[key.keyValue] | ' + scope.key.filterName + '"/>')(scope));
            } else {
                element.append($compile('<span ng-bind="columns[key.keyValue] | prettyHours:jiraConfig:$parent.pivotTableType == \'IssuePassedTimeByStatus\' && !$parent.workingTimeInStatus"/>')(scope));
            }
        }
    }
}]);

