var PivotKey = {};

PivotKey.Generic = function (keyName, keyValue, filterName) {
    this.keyName = keyName;
    this.keyValue = keyValue;
    this.filterName = filterName;
};

PivotKey.Issue = function(issue, worklog, configOptions) {
    this.issue = issue;
    this.keyName = "issue";
    this.keyValue = this.issue.key;
    this.worklog = worklog;
};

// jira-api/src/main/java/com/atlassian/jira/issue/comparator/KeyComparator.java
PivotKey.Issue.comparator = function KeyComparator(key1, key2) {
    if ((key1 == null) && (key2 == null)) {
        return 0;
    } else if (key1 == null) {
        return 1;
    } else if (key2 == null) {
        return -1;
    }

    var pair1 = key1.split('-');
    var pair2 = key2.split('-');

    // issue key may not have project key
    // data imported from Bugzilla may not comply with atlassian issue key format
    // this added to make jira more fault tolerant
    if ((pair1.length == 1) && (pair2.length == 1)) {
        var id1 = parseInt(pair1[0]);
        var id2 = parseInt(pair2[0]);
        var diff = id1 - id2;
        return diff;
    } else if (pair1.length == 1) {
        return 1;
    } else if (pair2.length == 1) {
        return -1;
    }

    var project1 = pair1[0];
    var project2 = pair2[0];

    // compare the project part
    for (var i = 0; i < project1.length && i < project2.length && !diff; i++) {
        diff = project1.charCodeAt(i) - project2.charCodeAt(i);
    }
    if (diff) {
        return diff;
    }

    // if one of the project parts is shorter than the other, that will be less than
    var diff = project1.length - project2.length;
    if (diff) {
        return diff;
    }

    // same project, compare numbers
    var id1 = parseInt(pair1[1]);
    var id2 = parseInt(pair2[1]);
    var diff = id1 - id2;

    return diff;
};

PivotKey.User = function(worklog) {
    this.user = worklog.author;
    this.keyName = "user";
    this.keyValue = worklog.author ? worklog.author.name : 'anonymous';
};
PivotKey.User.comparator = function(key1, key2) {
    return StringComparator(key1.keyValue, key2.keyValue);
};

PivotKey.getStatus = function(worklog, issue) {
    var workDate = TimesheetUtils.convertDate(worklog.started);
    var statusName = issue.fields.status.name;
    var statusId = issue.fields.status.id;
    var statusDate = new Date();
    var changelog = issue.changelog ? issue.changelog : {};
    var histories = changelog.histories ? changelog.histories : [];
    for (var i = 0; i < histories.length; i++) {
        var history = histories[i];
        var items = history.items || [];
        var created = TimesheetUtils.convertDate(history.created);
        for (var j = 0; j < items.length; j++) {
            var item = items[j];
            if (item.fieldtype == 'jira' && item.field == 'status' && workDate < created && statusDate > created) {
                statusName = item.fromString;
                statusId = item.from;
                statusDate = created;
            }
        }
    }
    return {name: statusName, id: statusId};
};

PivotKey.getStatuses = function(issue) {
    var changelog = issue.changelog ? issue.changelog : {};
    var histories = changelog.histories ? changelog.histories : [];
    var statuses = [];
    histories.map(function(history) {
        var created = TimesheetUtils.convertDate(history.created);
        var items = history.items || [];
        items.map(function(item) {
            if (item.fieldtype == 'jira' && item.field == 'status') {
                var status = {
                    id: item.from,
                    name: item.fromString,
                    date: created.getTime()
                };
                statuses.push(status);
            }
        });
    });
    statuses.push({ // latest
        id: issue.fields.status.id,
        name: issue.fields.status.name,
        date: Date.now()
    });
    TimesheetUtils.sortByProperty(statuses, 'date');
    return statuses;
};

PivotKey.getStatusKeys = function(worklog, issue, configOptions) {
    var statuses = PivotKey.getStatuses(issue);
    var timespent = worklog.timeSpentSeconds;
    var workDate = worklog.workDate.getTime(); // from WorkedTimePivotStrategy
    var result = [];
    var i = 0;
    while (i < statuses.length - 1 && workDate > statuses[i].date) i++; // skip passed statuses
    for (/* continue */; i < statuses.length - 1 && timespent > 0 && workDate < statuses[i].date; i++) {
        var status = statuses[i];
        var elapsed = PivotKey.getTimeInStatus(new Date(workDate), new Date(status.date), configOptions);
        var statusKey = {
            key: new PivotKey.Status(status.name, status.id, status.date),
            timespent: Math.min(timespent, elapsed)
        };
        result.push(statusKey);
        // remaining work
        workDate = status.date;
        timespent -= statusKey.timespent;
    }
    if (timespent > 0) { // latest
        var status = statuses[statuses.length - 1];
        var statusKey = {
            key: new PivotKey.Status(status.name, status.id, status.date),
            timespent: timespent
        };
        result.push(statusKey);
    }
    return result;
};

PivotKey.getTimeInStatus = function(startDate, endDate, configOptions) {
  if (configOptions.workingTimeInStatus) {
    // side effect for PassedTimePivotStrategy.getEntries
    var startTime = startDate.getTime();
    startDate.setHours(9, 0, 0, 0);
    startTime = Math.max(startTime, startDate.getTime());
    var endTime = endDate.getTime();
    endDate.setHours(17, 0, 0, 0);
    startTime = Math.min(startTime, endDate.getTime()); // make sure start time is not later than end time
    endTime = Math.min(endTime, endDate.getTime());
    var start = new Date(startTime);
    var end = new Date(startTime);
    end.setHours(17, 0, 0, 0);
    var timeSpentSeconds = 0;
    while (start.getTime() < endTime) {
      if (!TimesheetUtils.isWeekend(start, configOptions)) {
          timeSpentSeconds += (Math.min(end.getTime(), endTime) - start.getTime()) / 1000;
      }
      var date = start.getDate() + 1;
      start.setDate(date);
      start.setHours(9, 0, 0, 0, 0);
      end.setDate(date);
    }
    return timeSpentSeconds;
  } else {
    return (endDate.getTime() - startDate.getTime()) / 1000; // timeSpentSeconds
  }
};

PivotKey.Status = function(worklog, issue, date) {
    if (angular.isString(worklog) && angular.isString(issue)) {
        this.keyValue = worklog; // status
        this.statusId = issue;
        this.statusDate = date;
    } else { // TODO: remove backup case
        var status = PivotKey.getStatus(worklog, issue);
        this.keyValue = status.name;
        this.statusId = status.id;
        this.statusDate = status.date;
    }
};
PivotKey.Status.comparator = function(key1, key2) {
    var order = ["10000", "1", "3", "4", "5", "6", "400", "401", "10001"];
    return order.indexOf(key1.statusId) - order.indexOf(key2.statusId);
};

PivotKey.HourOfTheDay = function(date) {
	this.keyName = "dayOfTheWeek";
    this.date = TimesheetUtils.truncateMinutes(date);
    this.keyValue = this.date.getTime();
};

PivotKey.DayOfTheWeek = function(date) {
	this.keyName = "dayOfTheWeek";
    this.date = TimesheetUtils.truncateTime(date);
    this.keyValue = this.date.getTime();
};

PivotKey.WeekOfTheYear = function(date, options) {
    this.keyName = "weekOfTheYear";
    this.startDate = TimesheetUtils.getPrevMonday(date, options.reportingDay);
    this.endDate = TimesheetUtils.addDays(date, 7);
    this.weekNumber = TimesheetUtils.getNormalizedWeekNumber(date); //normalize for correct ordering
    this.keyValue = this.startDate.getTime();
};

PivotKey.MonthOfTheYear = function(date) {
    this.keyName = "monthOfTheYear";
    this.startDate = TimesheetUtils.getFirstDayOfTheMonth(date);
    this.endDate = TimesheetUtils.getLastDayOfTheMonth(date);
    this.monthKey = TimesheetUtils.getMonthKey(date);
    this.keyValue = this.startDate.getTime();
};

PivotKey.makeIssueFieldKey = function(fieldName, fieldObject) {
    var NULL_VALUE = "&nbsp;";
    var sprintRegex = /com\.atlassian\.greenhopper\.service\.sprint\.Sprint@[\d\w]+\[(.+)?\]/;

    var getNameOrKey = function(field) {
        var m;
        if (angular.isObject(field) && field.hasOwnProperty('name')) {
            return field.name;
        } else if (angular.isObject(field) && field.hasOwnProperty('key')) {
            return field.key;
        } else if (angular.isObject(field) && field.hasOwnProperty('value')) {
            return field.value + (field.hasOwnProperty('child') ? ':' + getNameOrKey(field.child) : '');
        } else if (angular.isString(field) && (m = field.match(sprintRegex))) {
            var splits = m[1].split(',');
            for (var i = 0; i < splits.length; i++) {
                var split = splits[i];
                if (split.indexOf('name=') == 0) {
                    return split.substr(5);
                }
            }
            return NULL_VALUE;
        } else {
            return field;
        }
    };

    var getValue = function(field) {
        if (TimesheetUtils.isDate(field)) {
            return TimesheetUtils.truncateTime(moment(field).toDate());
        } else if (Array.isArray(field)) {
            var value = '';
            field.forEach(function(v) {
                if (value.length > 0) {
                    value += ', ';
                }
                value += getNameOrKey(v);
            });
            return value;
        } else {
            return getNameOrKey(field);
        }
    };

    var getFieldValueObject = function(issue) {
        var value = issue.fields[fieldName];
        if (fieldObject && fieldObject.schema && fieldObject.schema.custom == 'com.pyxis.greenhopper.jira:gh-epic-link') {
            if (value) {
                value = {key: value};
            } else if (issue.fields.issuetype.name == 'Epic') {
                value = {key: issue.key};
            }
        }
        if (fieldName == 'project') {
            value = NULL_VALUE;
        }
        if (!value) {
            if (fieldName == 'parent') {
                value = {key : issue.key};
            } else if (fieldName == 'projectcategory' && issue.fields.project.projectCategory) {
                value = issue.fields.project.projectCategory;
            } else {
                value = NULL_VALUE;
            }
        }
        return value;
    };

    var result = function(issue, worklog, configOptions) {
        this._issue = issue;
        this.field = getFieldValueObject(issue);
        this.project = configOptions.collapseFieldGroups == true && fieldName != 'project'
            ? {name: ''}
            : this._issue.fields.project;
        this.keyName = fieldName;
        this.keyValue = this.project.name + ":" + getValue(this.field);
    };

    result.comparator = StringComparator;

    return result;
};

function StringComparator(key1, key2) {
    if ((key1 == null) && (key2 == null)) {
        return 0;
    } else if (key1 == null) {
        return 1;
    } else if (key2 == null) {
        return -1;
    }

    return key1.localeCompare(key2);
}

PivotKey.groupByWorkedUserKey = function(issue, worklog, configOptions) {
    var NULL_VALUE = "&nbsp;";
    this._issue = issue;
    this._worklog = worklog;
    this.field = this._worklog.author || NULL_VALUE;
    this.keyName = "workeduser";
    this.keyValue = this.field.displayName || this.field.name || NULL_VALUE;
};

PivotKey.groupByWorkedUserKey.comparator = StringComparator;

PivotKey.groupByWorkedDateKey = function(issue, worklog, configOptions) {
    var NULL_VALUE = "&nbsp;";
    this._issue = issue;
    this._worklog = worklog;
    this.field = this._worklog.started || NULL_VALUE;
    this.keyName = "workeddate";
    this.keyValue = this.field;
};

PivotKey.groupByWorkCreatedKey = function(issue, worklog, configOptions) {
    var NULL_VALUE = "&nbsp;";
    this._issue = issue;
    this._worklog = worklog;
    this.field = this._worklog.created || NULL_VALUE;
    this.keyName = "workcreated";
    this.keyValue = this.field;
};


PivotKey.groupByCommentFirstwordKey = function(issue, worklog, configOptions) {
    var NULL_VALUE = "&nbsp;";
    this._issue = issue;
    this._worklog = worklog;
    if (worklog.comment) {
        var i = worklog.comment.indexOf(' ');
        this.field = i > 0 ? worklog.comment.substring(0, i) : worklog.comment;
    } else {
        this.field = NULL_VALUE;
    }
    this.keyName = "commentfirstword";
    this.keyValue = this.field;
};

PivotKey.groupByWorkLabelKey = function(issue, worklog, configOptions) {
    var NULL_VALUE = "&nbsp;";
    this._issue = issue;
    this._worklog = worklog;
    var tags = TimesheetUtils.getTags(worklog.comment);
    if (tags && tags.length > 0) {
        tags.sort();
        this.field = tags.join(", ");
    } else {
        this.field = NULL_VALUE;
    }
    this.keyName = "worklabel";
    this.keyValue = this.field;
};

PivotKey.groupByWorkedDateKey.comparator = StringComparator;
